import React, {Component} from 'react'
import {Route} from 'react-router-dom'
import Main from './main'
import Developer from './developer'
import Comm from './commu'
import Headroom from 'react-headroom'
import NavBar from './navBar'
import firebase from 'firebase'
import {Login, MyPage} from './commu'
import {Verifyemail} from './verifyemail'

firebase.initializeApp({
  apiKey: "AIzaSyBGoMY8dwckJKSD_zVyvoJeAbQwu3H_1ko",
  authDomain: "team-quest-base-web.firebaseapp.com",
  databaseURL: "https://team-quest-base-web.firebaseio.com",
  projectId: "team-quest-base-web",
  storageBucket: "team-quest-base-web.appspot.com",
  messagingSenderId: "948102296922"
}, { timestampsInSnapshots: true })
firebase.firestore().settings({ timestampsInSnapshots: true })
export default class RoutePage extends Component {
  state = {
    unpin(){},
    pin(){},
    isPinned: Boolean(),
    fullArticles: [
      
    ]
  }
  swipeHandler = () => {
    this.setState(() => ({
      isPinned: this.refs.headroom.state.state
    }))
  }
  componentDidMount() {
    this.setState({
      pin: this.refs.headroom.pin,
      unpin: this.refs.headroom.unpin
    })
  }
  setRootState = (key, value) => {
    this.setState(() => ({
      [key]: value
    }))
  }
  getRootState = (key) => {
    return this.state[key]
  }
  render() {
    return (
      <div>
        <Headroom ref="headroom">
          <NavBar ref="navBar"/>
        </Headroom>
        <div ref="routeContain">
          <Route exact path="/" render={() => ((<Main pins={this.state} firestore={firebase.firestore} swipeHandler={this.swipeHandler} isPinned={this.state.isPinned}/>))}/>
          <Route path="/developer" component={Developer}/>
          <Route path="/login" exact render={() => ((<Login firebase={firebase}/>))}/>
          <Route path="/community" exact render={() => ((<Comm firebase={firebase} setRootState={this.setRootState} getRootState={this.getRootState}/>))}/></div>
          <Route path="/account" exact render={() => (
            <MyPage auth={firebase} />
          )} />
          <Route path="/emailverify" exact render={() => (
            <Verifyemail auth={firebase.auth} />
          )} />
      </div>
    )
  }
}