import React, { Component } from "react";
import "./App.css";
import "./article.css";
import ReactFullpage from '@fullpage/react-fullpage'
class Main extends Component {
  async componentWillMount() {
    this.setState({
      post: JSON.parse(
        (await this.props.firestore()
          .collection("dev")
          .doc("posts")
          .get()).data().data
      )
    })
  }
  state = {
    post: [
      { key: "1HISTORY", content: "잠시 기다려주세요", bg: "./img/bg/0.png" },
      { key: "1HISTORY", content: "잠시 기다려주세요", bg: "./img/bg/0.png" },
      { key: "1HISTORY", content: "잠시 기다려주세요", bg: "./img/bg/0.png" },
      { key: "1HISTORY", content: "잠시 기다려주세요", bg: "./img/bg/0.png" }
    ],
    lastScrollState: Number()
  }
  render() {
    return (
      <ReactFullpage onLeave={(a, b, where) => {
        if(where === 'down') this.props.pins.unpin()
        else this.props.pins.pin()
        this.props.swipeHandler()
      }} render={() => {
        return <ReactFullpage.Wrapper>
          {this.state.post.map((v, i) => (
            <div className="section" key={i}>
              <div
                style={{
                  backgroundImage: `url(${require(`${v.bg}`)})`
                }}
                className={`articleDiv ${this.props.isPinned}`}
              >
                <h1>{v.key}</h1>
                <section dangerouslySetInnerHTML={{ __html: v.content }} />
              </div>
            </div>
          ))}
        </ReactFullpage.Wrapper>
      }} />
    );
  }
}
export default Main;
