import React from 'react'
import Fuse from 'fuse.js'

class CommentsView extends React.Component {
  state = {
    replyComments: []
  }
  componentDidMount() {
    const { articleFirestore, nested, getRootState, setRootState, articleNumber } = this.props
    if (nested) return
    articleFirestore.collection('comment').onSnapshot(query => {
      this.setState({ replyComments: [] })
      query.forEach((doc) => {
        this.setState((state, callback) => ({
          replyComments: [...state.replyComments, doc.data()]
        }))
      })
      const fullArticles = getRootState('fullArticles')
      const contentOnlyReplyComments = this.state.replyComments.map((x) => x.content)
      fullArticles[articleNumber].contentOnlyComments = [
        ...fullArticles[articleNumber].contentOnlyComments,
        ...contentOnlyReplyComments
      ]
      setRootState('fullArticles', fullArticles)
      window.fullArticles = fullArticles
      window.Fuse = Fuse
    })
  }

  render() {
    const { image, name, contents, inputBox, date, setReplyTo, nested } = this.props
    return (
      <div style={{
        marginTop: '10px',
        paddingLeft: nested ? '40px' : '20px'
      }} onClick={nested ? undefined : () => {
        setReplyTo(date)
        inputBox.value += ` @${name}`
      }}>
        <img src={image ? image : 'https://cdn2.iconfinder.com/data/icons/people-80/96/Picture1-512.png'} style={{
          width: '30px',
          borderRadius: '50px',
        }} alt="프로필 이미지" />
        <div style={{
          display: 'inline-block',
          width: 'calc(100% - 50px)',
          verticalAlign: 'middle',
          boxSizing: 'border-box',
          paddingLeft: '10px',
          fontSize: '0.8rem'
        }}>
          <b>{name}</b>
          <div>{contents}</div>
        </div>
        {
          this.state.replyComments ? this.state.replyComments.map((v) => <CommentsView key={encodeURI(v.content)} image={v.profileImage} contents={v.content} name={v.author} nested={true} />) : undefined
        }
      </div>
    )
  }
}

export default class extends React.Component {
  addComment = (commentContents) => {
    if (!commentContents) {
      this.props.alert('댓글 내용이 없습니다')
      return
    }
    const { date, firebase: {
      firestore, auth
    } } = this.props
    const nowDate = +new Date()

    if (this.state.replyTo) {
      firestore().collection('post').doc(String(date)).collection('comments').doc(String(this.state.replyTo)).collection('comment').doc(String(nowDate)).set({
        author: auth().currentUser.displayName,
        content: commentContents,
        date: nowDate,
        profileImage: auth().currentUser.photoURL,
        parent: this.state.replyTo
      })
      return
    }

    firestore().collection('post').doc(String(date)).collection('comments').doc(String(nowDate)).set({
      author: auth().currentUser.displayName,
      content: commentContents,
      date: nowDate,
      profileImage: auth().currentUser.photoURL
    })
  }
  increaseHeart = () => {
    const { date, uid, firebase: {
      firestore
    }, alert } = this.props
    if(!uid) {
      alert('로그인이 필요합니다', 'error')
      return
    }
    firestore().collection('post').doc(String(date)).get().then(snap => {
      let heartedUser = snap.data().heartCount
      if (this.isHearted()) {
        heartedUser[heartedUser.indexOf(uid)] = undefined
        firestore().collection('post').doc(String(date)).set({
          heartCount: heartedUser.filter(Boolean)
        }, {
            merge: true
          })
      } else {
        firestore().collection('post').doc(String(date)).set({
          heartCount: [...heartedUser, uid]
        }, {
            merge: true
          })
      }
    })
  }
  isHearted = () => {
    return this.props.heartCount.includes(this.props.uid)
  }
  componentDidMount = () => {
    const { firebase: { firestore }, date, setRootState, getRootState, articleNumber } = this.props

    firestore().collection('post').doc(String(date)).collection('comments').onSnapshot((query) => {
      const comments = []
      query.forEach((doc) => {
        comments.push({
          ...doc.data(),
          articleFirestore: firestore().collection('post').doc(String(date)).collection('comments').doc(String(doc.data().date))
        })

        this.setState(() => ({ comments }))
        const contentOnlyComments = comments.map(x => {
          return x.content
        })
        const fullArticles = getRootState('fullArticles')
        fullArticles[articleNumber] = {
          ...fullArticles[articleNumber],
          contentOnlyComments,
        }
        setRootState('fullArticles', fullArticles)
      })
    })
  }
  setReplyTo = (where) => {
    this.setState({
      replyTo: where
    })
  }
  getReplyTo = () => {
    return this.state.replyTo
  }
  state = {

  }
  render() {
    const { isMine, photoURL, title, author, date, content, heartCount } = this.props

    return (
      <article>
        {isMine}
        <div className="articleCon">
          <div className="imgCon">
          <img src={photoURL ? photoURL : 'https://cdn2.iconfinder.com/data/icons/people-80/96/Picture1-512.png'} alt="프로필 이미지" />
          </div>
          <div className="contentCon">
            <div className="headCon">
              <h1>{title}</h1>
              <span>{author}</span>
              <span>。{(() => {
                const formattedDate = new Date(date)
                return `${formattedDate.getFullYear()}년 ${formattedDate.getMonth() + 1}월 ${formattedDate.getDate()}일 ${formattedDate.getHours()}시:${formattedDate.getMinutes() < 10 ? '0' + formattedDate.getMinutes() : formattedDate.getMinutes()}분`
              })()}</span>
            </div>
            <div className="content" dangerouslySetInnerHTML={{ __html: content }} />
            <div className="commentIcons">
              <i className="far fa-heart" onClick={this.increaseHeart} style={{
                color: this.isHearted() ? 'red' : ''
              }}></i>
              <span style={{
                color: this.isHearted() ? 'red' : ''
              }}>{heartCount.length} </span>
              <i className="far fa-comment" style={{
                marginLeft: '15px'
              }}></i>
              <span>{this.state.comments ? this.state.comments.length : undefined} </span>
            </div>
            <div>
              {
                this.state.comments ? this.state.comments.map((v, i) => {
                  return <CommentsView
                    name={v.author}
                    contents={v.content}
                    image={v.profileImage}
                    date={v.date}
                    key={i}
                    id={date}
                    inputBox={this.inputBox}
                    setReplyTo={this.setReplyTo}
                    articleFirestore={v.articleFirestore}
                    setRootState={this.props.setRootState}
                    getRootState={this.props.getRootState}
                    articleNumber={this.props.articleNumber}
                  />
                }) : undefined
              }
            </div>
            <input placeholder="댓글을 달아보세요!" style={{
              marginLeft: 20,
              marginTop: 30,
              border: '2px solid hsl(200,100%,28%)',
              padding: 5,
              height: 50,
              borderRadius: 5,
              fontSize: '0.8rem',
              width: 'calc(100% - 50px)',
              display: 'inline-block'
            }} onKeyDown={(e, i) => {
              if (e.key === 'Enter') {
                this.addComment(e.target.value, this.state.replyTo)
                e.target.value = ''
                this.setState(() => ({
                  replyTo: false
                }))
              }
            }} ref={(ref) => {
              this.inputBox = ref
            }} />
          </div>
        </div>
      </article>
    )
  }
}