import React, { Component } from 'react';
import './commu.css'
import Article from './commuArticle'
import Editor from 'tui-editor'
import './github.css'
import 'tui-editor/dist/tui-editor.css'
import 'tui-editor/dist/tui-editor-contents.min.css'
import './lib/codemirror.css'
import { Fab, FabButton, FabActions, FabAction } from 'react-fab'
import 'react-fab/dist/main.scss';
import Alert from 'react-s-alert';
import 'react-s-alert/dist/s-alert-default.css';
import 'react-s-alert/dist/s-alert-css-effects/stackslide.css';
import StyledFirebaseAuth from 'react-firebaseui/StyledFirebaseAuth';
import Fuse from 'fuse.js'
import { auth } from 'firebase';

class Login extends Component {
  state = {
    login: false
  }
  render() {
    const { firebase } = this.props
    return (
      <React.Fragment>
        <div className="headerpadding" />
        <div className="modal">
        <StyledFirebaseAuth uiConfig={{
          signInFlow: 'popup',
          signInOptions: [
            firebase.auth.GoogleAuthProvider.PROVIDER_ID,
            firebase.auth.EmailAuthProvider.PROVIDER_ID,
          ],
          credentialHelper: 'googleyolo',
          callbacks: {
            // Avoid redirects after sign-in.
            signInSuccessWithAuthResult: (...params) => {
              window.location.href = '/community'
            }
          }
        }} firebaseAuth={firebase.auth()} />
      </div>
      </React.Fragment>
    )
  }
}

class MyPage extends Component {
  state = {
    user: Object()
  }
  componentDidMount = async () => {
    auth().onAuthStateChanged(user => this.setState(() => ({user})))
  }
  render() {
    return (
      <div
        style={{
          padding: '20px',
          paddingTop: '100px',
          backgroundColor: 'white',
          width: '300px',
          textAlign: 'center'
        }}
        className="plain"
      >
        <h1 style={{
          fontSize: '30px',
          fontWeight: 500,
          display: 'block'
        }}>
          My Info
        </h1>
        <p>
        이름: <input value={this.state.user.displayName} />
        </p>
        <p>
        비밀번호: <button className="send_email">이메일 전송</button>
        </p>
        <p>
        이메일 변경: <button className="send_email">이메일 전송</button>
        </p>
      </div>
    )
  }
}

let toats
export class Comm extends Component {
  state = {
    articles: [],
    isEditorVisible: false,
    uid: String(),
    originalArticles: []
  }
  sendPost = () => {
    const { firebase } = this.props
    const content = toats.getHtml()
    const timeStamp = +new Date()
    firebase.firestore().collection("post").doc(String(timeStamp)).set({
      content,
      heartCount: [],
      author: firebase.auth().currentUser.displayName,
      date: timeStamp,
      photoURL: firebase.auth().currentUser.photoURL,
      title: content.includes('<h1>') ? content.split('<h1>')[1].split('</h1>')[0].split(/<[^>]*>/).join('') : `${firebase.auth().currentUser.displayName}님의 글`
    }).catch((err) => {
      alert('글을 업로드하지 못했습니다! 나중에 다시 시도해주세요')
    })
    if (document.getElementsByClassName('tui-editor-contents')[1].innerHTML) {
      document.getElementsByClassName('tui-editor-contents')[1].innerHTML = ''
    } else {
      document.getElementsByClassName('tui-editor-contents')[0].innerHTML = ''
    }

    this.setState({
      isEditorVisible: !(this.state.isEditorVisible)
    })
  }
  componentWillMount() {

  }
  componentDidMount() {
    const { firebase, setRootState } = this.props
    window.firebase = firebase
    
    firebase.firestore().collection('post').onSnapshot(snap => {
      const articles = []
      snap.forEach((doc) => {
        articles.push({
          title: doc.data().title,
          author: doc.data().author,
          date: doc.data().date,
          content: doc.data().content,
          photoURL: doc.data().photoURL,
          comments: doc.data().comments,
          heartCount: doc.data().heartCount,
          uid: this.state.uid,
          isMine: (firebase.auth().currentUser && doc.data().author === firebase.auth().currentUser.displayName),
        })
      })
      this.setState({
        articles: [...articles].reverse(),
        originalArticles: [...articles].reverse()
      })
      const filteredArticles = articles.map(x => ({
        title: x.title,
        author: x.author,
        content: x.content,
        date: x.date
      }))
      setRootState('fullArticles', [...filteredArticles].reverse())
    })
    firebase.auth().onAuthStateChanged(user => {
      if(user && !user.emailVerified) {
        alert('이메일 인증이 필요합니다!')
        window.location.href = '/emailverify'
      }
      this.setState({
        loginStatus: user,
        uid: user && user.uid
      })
    });
    toats = new Editor({
      el: this.refs.editor,
      initialEditType: 'wysiwyg',
      previewStyle: 'vertical',
      height: '300px',
      initialValue: '당신만의 글을 써보세요!'
    })
    this.setState({
      mode: toats.currentMode
    })
  }
  search = () => {
    const { value } = this.searchbox
    if (!value) {
      this.setState((state) => ({
        articles: state.originalArticles
      }))
      return
    }
    const fullArticles = this.props.getRootState('fullArticles')
    const fuse = new Fuse(fullArticles, {
      shouldSort: true,
      threshold: 0.2,
      location: 0,
      distance: 100,
      maxPatternLength: 32,
      minMatchCharLength: 1,
      keys: [
        "author",
        "content",
        "contentOnlyComments"
      ]
    })
    const searchResult = fuse.search(value)
    const filteredArticles = this.state.originalArticles.filter((article) => {
      return searchResult.some(filteredArticle =>
        article.date === filteredArticle.date
      )
    })
    this.setState(() => ({
      articles: filteredArticles
    }))
  }
  alert(title, type = 'error') {
    Alert[type](title, {
      position: 'top-right',
      effect: 'stackslide'
    });
  }
  render() {
    const { firebase } = this.props
    return (
      <React.Fragment>
        
        <div id="editorBox" className={this.state.isEditorVisible ? 'visible' : 'invisible'} onClick={() => {
            this.setState({
              isEditorVisible: !(this.state.isEditorVisible)
            })
          }}>
            <div id="innerBox" onClick={(e) => {
              e.stopPropagation()
            }}>
              <div ref="editor" id="tui-editor"></div>
              <div className="uploadCon" onClick={this.sendPost}>
                <i className="fab fa-hubspot"></i><span>포스트</span>
              </div>
            </div>
          </div>
        <div
        style={{
          paddingTop: '80px'
        }}
        className={this.props.navbarStatus}>
        <Alert stack={{ limit: 3 }} />

        <Fab style={{ right: '20px', bottom: '20px', position: 'fixed' }}>

          <FabButton>
            +
          </FabButton>

          <FabActions>
            {
              this.state.loginStatus ? <FabAction
                onClick={async () => {
                  this.alert('로그아웃 되었습니다', 'success')
                  return await firebase.auth().signOut()
                }}
                tooltip="로그아웃"
              >
                <i className="fas fa-sign-out-alt" />
              </FabAction> : undefined
            }

            {
              this.state.loginStatus ? <FabAction
                onClick={async () => {

                }}
                tooltip="내 정보"
              >
                <i className="fas fa-sign-out-alt" />
              </FabAction> : undefined
            }

            {this.state.loginStatus ? <FabAction
              onClick={() => {
                this.searchbox.value = auth().currentUser.displayName
                this.search()
              }}
              tooltip="내가 작성한 글 보기"
            >
              <i className="fas fa-male"></i>
            </FabAction> : undefined}

            {this.state.loginStatus ? <FabAction
              onClick={() => {
                this.setState({
                  isEditorVisible: !(this.state.isEditorVisible)
                })
              }}
              tooltip="글 쓰기"
            >
              <i className="fas fa-pen-fancy" />
            </FabAction> : undefined}

            {!this.state.loginStatus ? <FabAction
              onClick={() => {
                window.location.href = '/login'
              }}
              tooltip="로그인"
            >
              <i className="fas fa-sign-in-alt" />
            </FabAction> : undefined}

          </FabActions>

        </Fab>
        <div className="plain commu">
          <input id="searchbar" placeholder="검색.." onChange={this.search} ref={(ref) => {
            this.searchbox = ref
          }} /> <div id="searchicon">
            <i className="fas fa-search"></i>
          </div>
          {
            this.state.articles.map((v, i) => {
              return <Article {...v} key={v.date} setRootState={this.props.setRootState} getRootState={this.props.getRootState} alert={this.alert} articleNumber={i} firebase={firebase} />
            })
          }
        </div>
      </div>
    
      </React.Fragment>
      )
  }
};

export { Login, MyPage };
export default Comm;