import React from 'react'
export class Verifyemail extends React.Component {
    state = {
        user: Object()
    }
    componentDidMount() {
        const { auth } = this.props
        auth().onAuthStateChanged((user) => {
            if(user.emailVerified) {
                window.location.href = '/community'
                return
            }
            window.user = user
            user.sendEmailVerification()
            this.setState(() => ({
                user
            }))
        })
    }
    getProtectedEmail(email = '@') {
        const processedUsername = email.split('@')[0].split('').map((x, i) => i <4 ? x : '*').join('') + '@'
        const processedDomain = email.split('@')[1].split('').map((x, i) => i <4 ? x : '*').join('')
        return [...processedUsername, ...processedDomain]
    }
    render() {
        return (
            <div className="plain" style={{
                padding: '20px',
                paddingTop: '100px',
                backgroundColor: 'white'
            }}>
                <h1 style={{
                    fontSize: '30px'
                }}>
                {this.state.user.displayName}님, 메일함을 확인해주세요!
                </h1>
                <p>
                이메일 인증을 위한 링크가 {this.getProtectedEmail(this.state.user.email)}로 전송되었습니다. 링크를 클릭하시고 페이지를 새로고침해주세요!
                </p>
            </div>
        )
    }
}